function parseQuery(queryString) {
    var query = {};
    var pairs = (queryString[0] === '?' ? queryString.substr(1) : queryString).split('&');
    for (var i = 0; i < pairs.length; i++) {
        var pair = pairs[i].split('=');
        query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || '');
    }
    return query;
}

const drawDotAtCenter = (canvas) => {
    const ctx = canvas.getContext('2d');

    // Calculate the center of the canvas
    const centerX = canvas.width / 2;
    const centerY = canvas.height / 2;

    // Draw a dot at the center
    ctx.beginPath();
    ctx.arc(centerX, centerY, 5, 0, 2 * Math.PI); // 5 is the radius of the dot
    ctx.fillStyle = 'red';
    ctx.fill();
}



const drawDirections = (canvas) => {

    const ctx = canvas.getContext('2d');
    const centerX = canvas.width / 2;
    const centerY = canvas.height / 2;
    const radius = Math.min(centerX, centerY);
    console.log(centerX)
    ctx.clearRect(0, 0, canvas.width, canvas.height);


    // Draw the circle
    ctx.beginPath();
    ctx.arc(centerX, centerY, radius, 0, 2 * Math.PI);
    ctx.stroke();

    // Directions in degrees
    const directions = [
        { angle: 0, label: '23' }, // North
        { angle: 45, label: '' }, // North-East
        { angle: 90, label: '' }, // East
        { angle: 135, label: '' }, // South-East
        { angle: 180, label: '' }, // South
        { angle: 225, label: '' }, // South-West
        { angle: 270, label: '' }, // West
        { angle: 315, label: '' } // North-West
    ];

    // Convert degrees to radians
    directions.forEach(direction => {
        const angleInRadians = (direction.angle + 22.5) * (Math.PI / 180);
        const x = centerX + radius * Math.cos(angleInRadians);
        const y = centerY + radius * Math.sin(angleInRadians);

        // Draw line
        ctx.beginPath();
        ctx.moveTo(centerX, centerY);
        ctx.lineTo(x, y);
        ctx.stroke();

        // Draw label
        ctx.fillText(direction.label, x, y);
    });
    drawDotAtCenter(canvas)
}



